'use client';
import { costemStorage } from "@/utils";
import axios from "axios";
import { useEffect, useState } from "react"

//! Ïù¥Í±∞ Í∏ÄÎ°úÎ≤åÎ°ú ÎßåÎì§Ïñ¥Ï§òÏïºÌï®
/**Î°úÍ∑∏Ïù∏ Hook*/
export const useAuth = () => {
    const password = costemStorage.local.get(`auth`,``);
    const [auth, setAuth] = useState<boolean>(false);
    const [loding, setLoding] = useState(false);

    /**APIÎ°ú passwordÎ•º Î≥¥ÎÇ¥ ÌôïÏù∏ÌïòÍ∏∞*/
    const authPath = async (pw: string) => {
        setLoding(true);
        try {
            const response = await axios.post(
                '/api',
                { password: pw, },
                { headers: { 'Content-Type': 'application/json', } }
            );
            if (response.status === 200) {
                costemStorage.local.set(`auth`, pw);
                setAuth(true);
            }
        }
        catch (error: any) {
            const errorMessage = (error.response?.data as { message?: string })?.message;
            if (errorMessage) { alert(errorMessage); }
            else { alert('Unexpected Error Occurred During API Execution'); }
        }
        finally { setLoding(false); }
    };

    /**Ïù∏Ï¶ù Ï¥àÍ∏∞Ìôî*/
    const initAuth = () => {
        costemStorage.local.remove('auth');
        setAuth(false);
        setLoding(false);
    }

    useEffect(()=>{
        if(password !== "") { authPath(password); }
    },[auth])

    return {
        /**Ïù∏Ï¶ùÏó¨Î∂Ä*/
        isAuth: auth,
        /**Î°úÍ∑∏Ïù∏*/
        login: authPath,
        /**Ïã§Ìñâ Ïó¨Î∂Ä*/
        loding: loding,
        /**Ïù∏Ï¶ù Ï¥àÍ∏∞Ìôî*/
        initAuth,
    };
}